.home-container-1
{
    min-height: 100vh;
    max-width: 1250px;
    width: 100%;
    margin: 0% auto;
    display: flex;
    justify-content: space-between;
}
.home-container-2
{
    max-width: 1100px;
    width: calc(100% - 164px);
    padding: 24px;
    /* border: 1px solid #d6d9dc; */
    box-sizing: border-box;
}
@media screen and (max-width: 760px)
{
    .home-container-2
    {
      max-width: 100%;
      width: 100%;
    }
}